import { Icon } from "@iconify/react";
import {
	Box,
	Button,
	Collapse,
	Divider,
	Grid,
	InputAdornment,
	List,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	SvgIcon
} from "@mui/material";
import map from "lodash/map";
import { useSnackbar } from "notistack";
import { useState } from "react";
import { useTranslation } from "react-i18next";

import { useMiddleware } from "../../../../Application";
import NumberTextField from "../../../../components/NumberTextField";
import pricify from "../../../../utils/pricify";
import { LoadingButton } from "@mui/lab";

const CheckListItem = ({ isActive, onClick, activeContent, ...props }) => {
	return (
		<>
			<ListItem disableGutters disablePadding dense>
				<ListItemButton selected={isActive} onClick={onClick}>
					<ListItemIcon>
						<SvgIcon
							component={Icon}
							icon={
								isActive
									? "solar:check-square-bold"
									: "solar:stop-linear"
							}
							color={isActive ? "primary" : "inherit"}
						/>
					</ListItemIcon>
					<ListItemText {...props} />
				</ListItemButton>
			</ListItem>
			<Collapse in={isActive}>{activeContent}</Collapse>
		</>
	);
};

const Form = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const { enqueueSnackbar } = useSnackbar();

	const middleware = useMiddleware("Customers-Club.Main");
	
	const [data, setData] = useState({ amount: 300000, gift: 0 })

	const predefinedAmounts = [
		{
			value: 300000,
			gift: 0,
			primary: "300 هزار تومان",
			secondary: "بدون اعتبار هدیه",
		},
		{
			value: 600000,
			gift: 60000,
			primary: "600 هزار تومان",
			secondary: "10 درصد اعتبار بیشتر",
		},
		{
			value: 1200000,
			gift: 180000,
			primary: "1 میلیون و 200 هزار تومان",
			secondary: "15 درصد اعتبار بیشتر",
		},
		{
			value: 2500000,
			gift: 500000,
			primary: "2 میلیون و 500 هزار تومان",
			secondary: "20 درصد اعتبار بیشتر",
		},
		{
			value: 5000000,
			gift: 1250000,
			primary: "5 میلیون تومان",
			secondary: "25 درصد اعتبار بیشتر",
		},
		{
			value: 9000000,
			gift: 2700000,
			primary: "9 میلیون تومان",
			secondary: "30 درصد اعتبار بیشتر",
		},
	];

    const [proccessing, setProccessing] = useState(false);
    const proccessCreditAsync = async () => {
        setProccessing(true);
        const response = await middleware.addCredit({
            creditAmount: Number(data.amount),
			giftAmount: Number(data.gift),
            returnUrl: window.location.href,
        });
        if (response.ok) {
            window.location.href = response.redirectUrl;
        } else {
            enqueueSnackbar('خطا در برقراری ارتباط');
        }
    }

	return (
		<Box>
			<NumberTextField
				fullWidth={true}
				// size="small"
				label="میزان اعتبار"
				dir="ltr"
				variant="filled"
				InputProps={{
					startAdornment: (
						<InputAdornment position="start">
							<small>تومان</small>
						</InputAdornment>
					),
				}}
				value={data.amount}
				onChange={(e) => setData({ ...data, amount: e.target.value })}
			/>

			<Box sx={{ my: 2 }} />

			<Grid container spacing={1}>
				{map(predefinedAmounts, (pre, index) => (
					<Grid item xs={12} sm={6}>
						<CheckListItem
							key={index}
							isActive={pre.value === Number(data.amount)}
							onClick={() => setData({ ...data, amount: pre.value, gift: pre.gift })}
							{...pre}
						/>
					</Grid>
				))}
			</Grid>

			<Divider sx={{ my: 2 }} />

			<List disablePadding>
				<ListItem>
					<ListItemText primary="مبلغ افزایش اعتبار" />
					<ListItemText primaryTypographyProps={{ fontWeight: "bold" }} sx={{ textAlign: "end" }}>
						{pricify(data.amount)}{` `}
						<small>تومان</small>
					</ListItemText>
				</ListItem>
				<ListItem>
					<ListItemText primary="10% مالیات بر ارزش افزوده" />
					<ListItemText primaryTypographyProps={{ fontWeight: "bold" }} sx={{ textAlign: "end" }}>
						{pricify(Number(data.amount) / 10)}{` `}
						<small>تومان</small>
					</ListItemText>
				</ListItem>
				<ListItem>
					<ListItemText primary="جمع مبلغ پرداختی" />
					<ListItemText primaryTypographyProps={{ fontWeight: "bold" }} sx={{ textAlign: "end" }}>
						{pricify(Number(data.amount) + Number((Number(data.amount) / 10)))}{` `}
						<small>تومان</small>
					</ListItemText>
				</ListItem>
			</List>

			<Divider sx={{ my: 2 }} />

			<List disablePadding>
				<ListItem>
					<ListItemText primary="اعتبار هدیه" />
					<ListItemText primaryTypographyProps={{ fontWeight: "bold" }} sx={{ textAlign: "end" }}>
						{pricify(Number(data.gift))}{` `}
						<small>تومان</small>
					</ListItemText>
				</ListItem>
				<ListItem>
					<ListItemText primary="مجموع اعتبار دریافتی" />
					<ListItemText primaryTypographyProps={{ fontWeight: "bold" }} sx={{ textAlign: "end" }}>
						{pricify(Number(data.amount) + Number(data.gift))}{` `}	
						<small>تومان</small>
					</ListItemText>
				</ListItem>
			</List>

			<Box sx={{ my: 2 }} />

			<LoadingButton
				variant="contained"
				onClick={proccessCreditAsync}
				loading={proccessing}
				size="large"
				sx={{ float: "right" }}
			>
				{t("پرداخت آنلاین")}
			</LoadingButton>
		</Box>
	);
};

export default Form;
