import {
	Alert,
	Box,
	Card,
	CardContent,
	Container,
	Divider,
	Grid,
	styled,
	Typography
} from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";

import { useTranslation } from "react-i18next";
import { useMiddleware, useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const AddCredit = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const middleware = useMiddleware("Customers-Club.Main");

	const task = useTask("Customers-Club.charge");

	const GUIDE = `
### نکات آموزشی

۱- حداقل مبلغ خرید 300 هزار تومان است. خرید کمتر از این مبلغ ممکن نیست. چرا؟ چون ما برای ارائه به دارایی به ازای هر خرید شما یک فاکتور صادر می کنیم ، همه فاکتورها نیز پرینت و مهر و امضاء می شود ، کاهش مبلغ حداقل خرید ، باعث افزایش چند برابری فاکتورها می شود.

۲- قیمت هر پیامک فارسی 129 تومان است.

همچنین به کلیه خرید ها %۱۰ مالیات ارزش افزوده اضافه می شود.

3- اگر در پرداخت آنلاین پول از حساب شما کسر شد ولی حساب شما شارژ نشد لطفا حداقل 30 دقیقه صبر کنید تا تراکنش شما بصورت سیستمی بررسی شود و در صورت موفق بودن حساب شما شارژ خواهد شد. اگر پس از 30 دقیقه حساب شما شارژ نشد تراکنش ناموفق بوده است و مبلغ به حساب شما بازخواهد گشت.

4- برای مصرف شارژ خریداری شده هیچ محدودیتی وجود ندارد ولی در صورت افزایش تعرفه از سمت اپراتور های موبایل باقیمانده اعتبار شما با تعرفه جدید محاسبه میشود ، اما اگر از دستگاه استفاده کنید و روزانه فقط حدود 10 مشتری شماره خود را وارد دستگاه کنند و یکی از سرویس های نظرسنجی ، جایزه و .. را نیز فعال کرده باشید در هر ماه حدود 2 هزار پیامک مصرف می کنید که هزینه بسیار ناچیزی برای کسب و کار شما دارد ولی می تواند در فروش شما تاثیر زیادی بگذارد.
`;

	return (
		<TabView {...task}>
			<ResponsiveContainer>
				<Container disableGutters>
					<Card variant="outlined">
						<CardContent sx={{ p: 3 }}>
							<Grid container spacing={3}>
								<Grid item xs={12} md={6}>
									<Typography variant="h6" mb={1}>{t("افزایش اعتبار پیامکی")}</Typography>
									<Typography color="text.secondary" mb={1}>{t("مبلغ مورد نظر خود را برای شارژ اعتبار انتخاب کنید.")}</Typography>

									<Form />
								</Grid>
								<Grid item xs={12} sm={6}>
									<Alert color="info">
										<MarkdownContainer>
											<Markdown breaks={true}>
												{DOMPurify.sanitize(GUIDE)}
											</Markdown>
										</MarkdownContainer>
									</Alert>
								</Grid>
							</Grid>
						</CardContent>
					</Card>
				</Container>

				{/* {inject('Documentations', 'BooksList.Footer', { data, loading, reload, ...injectedHooks })} */}
			</ResponsiveContainer>
		</TabView>
	);
};

export default AddCredit;
